<template>
  <div class="app-wrapper">
    <div class="top-menu">
      <div class="menu-container">
        <div class="logo">
          HelloWar - {{ environment }}
        </div>
        <el-menu
          mode="horizontal"
          :default-active="activeMenu"
          @select="handleSelect"
          background-color="#304156"
          text-color="#fff"
          active-text-color="#ffd04b"
          :ellipsis="false"
        >
          <el-menu-item v-for="item in menuItems" :key="item.path" :index="item.path">
            <el-icon><component :is="item.icon" /></el-icon>
            <span>{{ item.title }}</span>
          </el-menu-item>
        </el-menu>
        <div class="user-menu">
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link">
              Admin<el-icon class="el-icon--right"><arrow-down /></el-icon>
            </span>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item command="logout">Logout</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </div>
    </div>
    

    <div class="main-container">
      <div class="main-content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { menuItems } from '@/config/menu-config'
import { ArrowDown } from '@element-plus/icons-vue'

const router = useRouter()
const route = useRoute()

// 从环境变量中获取环境信息
const environment = import.meta.env.VITE_APP_ENV || 'development'

const activeMenu = computed(() => {
  return '/' + route.path.split('/')[1]
})

const handleSelect = (path: string) => {
  router.push(path)
}

const handleCommand = (command: string) => {
  if (command === 'logout') {
    localStorage.removeItem('token')
    router.push('/login')
  }
}
</script>

<style scoped>
.menu-container {
  display: flex;
  align-items: center;
  width: 100%;
}

.logo {
  padding: 0 20px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  white-space: nowrap;
}

.top-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  z-index: 1000;
  background-color: #304156;
}

.app-wrapper {
  padding-top: 80px;
  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sidebar-container {
  position: fixed;
  top: 60px;
  bottom: 0;
  left: 0;
  width: 200px;
  max-width: 20vw;
  z-index: 999;
}

.main-container {
  /* padding-top: 40px; */
  display: flex;
  flex: 1;
  overflow: hidden;
  max-height: 90vh;
}

.main-content {
  flex: 1;
  padding: 0;
  overflow: hidden;
}

.user-menu {
  margin-left: auto;
  padding: 0 20px;
}

.el-dropdown-link {
  color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  height: 60px;
}

.el-dropdown-link:hover {
  color: #ffd04b;
}

:deep(.el-icon--right) {
  margin-left: 8px;
}
</style> 