import { createRouter, createWebHistory } from 'vue-router'
import Layout from '@/layout/index.vue'
import { designTypes } from '@/config/menu-config'
import EmptyLayout from '@/layout/empty-layout.vue'
import AccountLayout from '@/layout/account-layout.vue'
import AnalysisLayout from '@/layout/analysis-layout.vue'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/login',
      component: () => import('@/views/login/index.vue'),
      meta: { requiresAuth: false }
    },
    {
      path: '/',
      component: Layout,
      redirect: '/design',
      children: [
        {
          path: 'design',
          component: EmptyLayout,
          children: [
            {
              path: '',
              component: () => import('@/views/design/home.vue'),
              name: 'DesignHome',
              meta: { 
                title: 'Design',
                icon: 'design',
                requiresAuth: true
              }
            },
            {
              path: ':name',
              component: () => import('@/views/design/index.vue'),
              name: 'Design',
              meta: { requiresAuth: true }
            }
          ]
        }
      ]
    },
    {
      path: '/account',
      component: Layout,
      children: [
        {
          path: '',
          component: AccountLayout,
          children:[
            // {
            //   path: '',
            //   component: () => import('@/views/account/index.vue'),
            //   name: 'Account',
            //   meta: { title: 'Account', icon: 'user' },
              
            // },
            {
              path: 'test-battle-records',
              component: () => import('@/views/account/test-battle-records.vue'),
              name: 'TestBattleRecords',
              meta: { title: '测试战斗记录', icon: 'list' }
            },
            {
              path: 'character-sessions',
              component: () => import('@/views/account/character-sessions.vue'),
              name: 'CharacterSessions',
              meta: { title: 'Character Sessions', icon: 'user' }
            }
          ]
        },
        
      ]
    },
    {
      path: '/analysis',
      component: Layout,
      children: [{
        path: '',
        component: AnalysisLayout,
        children:[
          {
            path:'',
            component: () => import('@/views/analysis/index.vue'),
            name: 'Analysis',
            meta: { title: 'Analysis', icon: 'chart' } 
          },
          {
            path: 'shop-simulator',
            component: () => import('@/views/analysis/shop-simulator.vue'),
            name: 'Shop Simulator',
            meta: { title: 'Test Shop Generator', icon: 'chart' }
          }
        ]
      }]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!localStorage.getItem('token')) {
      next('/login')
      return
    }
  }
  next()
})

export default router 